.drop {
    &__area {
        padding: 15px;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        background-color: rgba(97, 207, 87, 0.08);
        border: 2px dashed rgba(97, 207, 87, 0.25);
        border-radius: 7px;

        &.highlight {
            border-color: #61CF57;
            background-color: rgba(97, 207, 87, 0.4);
        }

        &.load {
            .drop__text--hide {
                display: initial;
            }
        }
    }

    &__img {
        max-width: 330px;
        margin-bottom: 35px;

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }

    &__text {
        p {
            font-weight: 700;
            line-height: 140%;
            text-transform: uppercase;
            text-align: center;
            font-size: fluid-font(1.6rem, 1.4rem);
        }

        p + p {
            margin-top: 10px;
        }

        &--hide {
            display: none;
        }
    }

    &__error {
        margin-top: 10px;
        color: #c90000;
    }

    &__form {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    &__btn {
        font-size: 1.8rem;
        line-height: 130%;
        letter-spacing: 0.02em;
        display: flex;
        align-items: center;
        justify-content: center;
        color: var(--clr-default-100);
        background-color: var(--clr-primary-400);
        box-shadow: inset 0px 4px 6px rgba(255, 255, 255, 0.25), inset 0px -4px 6px #20451d;
        transition: box-shadow $ms;
        border-radius: 3px;
        padding: 0 15px;
        height: 55px;
        min-width: 240px;
        overflow: hidden;
        margin-top: 15px;
        cursor: pointer;

        @media (hover) {
            &:hover {
                box-shadow: initial;
                transition: box-shadow $ms;
            }
        }
    }

    &__btn + &__text {
        margin-top: 15px;
    }

    &__file {

        &-elem {
            display: none;
        }

        &-item {
            display: none;
            align-items: center;
            padding: 15px 25px;
            margin-top: 20px;
            border: 1.5px solid rgba(97, 207, 87, 0.25);
            border-radius: 7px;
            
            @media (max-width: 460px) {
                flex-direction: column;
            }

            &-info {
                display: flex;
                align-items: center;
                margin-left: 15px;

                @media (max-width: 460px) {
                    margin-left: initial;
                    flex-direction: column;
                }
            }

            &-name {
                font-size: 2rem;
            }

            &-type {
                margin-left: 10px;
                margin-right: 10px;
                @media (max-width: 460px) {
                    margin: 10px 0;
                }
            }

            &.active {
                display: flex;
            }

            &-img {
                max-width: 52px;

                img {
                    width: 100%;
                    height: auto;
                    object-fit: cover;
                }

                @media (max-width: 460px) {
                    margin-bottom: 10px;
                }
            }
        }
    }
}
